import React, { Component } from 'react';
import "./calendar.less"
import SelectedPng from 'assets/img/daysel.png';
import ArrowPng from 'assets/img/arrow.png';
import MyPng from "assets/img/btn_my.png";
import CalbgPng from 'assets/img/calendar_bg.png';
import { getSelectedDate } from 'store/module/calendar';
import { connect } from 'react-redux';
import { getZodiac } from 'utils/general';

const Month = [
    ["1月 JANUARY", 31],["2月 FEBRUARY", 29],["3月 MARCH", 31],["4月 APRIL", 30],["5月 MAY", 31],["6月 JUNE", 30],
    ["7月 JULY", 31],["8月 AUGUST", 31],["9月 SEPTEMBER", 30],["10月 OCTOBER", 31],["11月 NOVEMBER", 30],["12月 DECEMBER", 31]
]

class Calendar extends Component {
    constructor(props) {
        super(props)
        this.state = {
            monthIndex: 0,
            day: 1
        }
    }

    componentDidMount() {
        if(this.props.selectedDate) {
            let now = this.props.selectedDate
            this.setState({
                monthIndex: now.month-1,
                day: now.day
            })
        } else {
            let now = new Date();
            this.setState({
                monthIndex: now.getMonth(),
                day: now.getDate()
            })
        }
    }

    getDaysByMonth() {
        let month = Month[this.state.monthIndex]
        let days = []
        for(let i=1; i<=month[1]; i++) {
            days.push(i)
        }
        return days;
    }

    onPrevMonth = () => {
        let index = this.state.monthIndex
        index--;
        if(index < 0) {
            index = 0
        }
        this.setState({monthIndex: index})
    }

    onNextMonth = () => {
        let index = this.state.monthIndex
        index++;
        if(index > Month.length-1) {
            index = Month.length-1
        }
        this.setState({monthIndex: index})
    }

    onSelectDay = (day) => {
        this.setState({day: day})
    }

    hanldeClose = () => {
        if(this.props.closeCalendar) {
            this.props.closeCalendar()
        }
    }

    hanldeStopClose = (e) => {
        e.stopPropagation();
    }

    handleEnter = () => {
        const { monthIndex, day } = this.state;
        let month = monthIndex + 1
        let zodiac = getZodiac(month, day)
        this.props.setCalendar({month, day})
        zodiac = zodiac?zodiac:{}
        if(this.props.onEnter) {
            this.props.onEnter(zodiac.key)
        }
    }

    render() {
        const { monthIndex, day } = this.state;
       return (
        <div className="calendar-root" onClick={this.hanldeClose}>
            <div className="btn">
                    <div style={{width: "84.5px", height:"124px", marginBottom: "42px"}} />
                    <div className="btn-cal">
                        <img src={MyPng} alt="" />
                        <div className="text">{day}</div>
                    </div>
                </div>
            <div className="content-box" onClick={this.hanldeStopClose}>
            <img className="bg" src={CalbgPng} alt="" />
            <div className="header">
                    <img style={{width:"31px",transform: "rotate(180deg)",marginLeft:"50px",cursor:"pointer"}} src={ArrowPng} alt="" onClick={this.onPrevMonth}/>
                    <div>{Month[monthIndex][0]}</div>
                        <img style={{width:"31px", marginRight:"50px",cursor:"pointer"}} src={ArrowPng} alt="" onClick={this.onNextMonth}/>
                    </div>
                    <div className="center">
                        <div className="day">
                            {
                                this.getDaysByMonth().map( (item) => {
                                    return <div className="dayitem" key={item} 
                                    onClick={()=>this.onSelectDay(item)}>
                                        {item === day && <img className="dayitem-img" src={SelectedPng} alt=""/>}
                                        <div className={item === day? "dayitem-text selected" : "dayitem-text"}>{item}</div>
                                    </div>
                                })
                            }
                        </div>
                    </div>
                    <div className="footer" onClick={this.handleEnter}>
                        进入 ENTRY
                    </div>
            </div>
        </div>
       )
   }
}

export default connect(state => ({
    selectedDate: getSelectedDate(state)
  }),
  dispatch => ({
      setCalendar: (date) => dispatch({ type: "SET_CALENDAR", doc: { date } })
  })
)(Calendar);