import React, { Component } from 'react';
import "./zodiacdesc.less"
import { ZODIAC } from 'utils/global';
import Calendar from 'components/Calendar/Calendar';
const TextWidth = {
    "aquarius": "814.5px","aries": "859px","cancer": "852.5px","capricorn": "833px","gemini": "853.5px","leo": "851px",
    "libra": "782.5px","pisces": "860.5px","sagittarius": "722.5px","scorpio": "848.5px","taurus": "857px","virgo": "847.5px"
}

class ZodiacDesc extends Component {
    constructor(props) {
        super(props)
        this.state = {
            selected: ""
        }
   }

   componentDidMount() {
        this.setState({
            selected: this.props.history.location.key
        })
   }

   getSelectedDay() {
       const { selected } = this.state;
       let sel = ZODIAC.find( item => item.key === selected)
       return sel ? sel.day : ""
    }

    handleCalendarEnter = (key) => {
        this.setState({
            selected: key
        })
        if(this.props.closeCalendar) {
            this.props.closeCalendar()
        }
    }

   render() {
       const { selected } = this.state;
       const { showCalendar, closeCalendar } = this.props;

       return (
            <div className="zodiacdesc-root">
                <video src={`video/zodiac/${selected}.mp4`} width="100%" height="100%" style={{objectFit: "fill"}} muted autoPlay loop/>
                <div className="list">
                        {ZODIAC && ZODIAC.map( (item,index) => {
                            return (
                                <div key={index} style={{position: "relative"}}>
                                    {selected===item.key && <div className="selected-line"></div>}
                                    <div className={selected===item.key? "li selected" : "li"} onClick={()=>{this.setState({selected: item.key})}}>{item.name}</div>
                                    {selected===item.key && <img className="selected-icon" style={{width: "69px"}} src={`image/icon/${selected}.png`} alt="" />}
                                </div>
                            )
                        })}
                </div>
                <div className="day">
                        {this.getSelectedDay()}
                </div>
                <div className="text">
                    <img style={{width: TextWidth[selected]}} src={`image/text/${selected}.png`} alt="" />
                </div>
                { showCalendar && <Calendar closeCalendar={closeCalendar} onEnter={this.handleCalendarEnter} />}
            </div>
       )
   }
}

export default ZodiacDesc;